*,
*::after,
*::before {
  box-sizing: border-box;
}

@font-face {
  font-family: "SF Pro Display";
  src: local("SF Pro Display"), local("SF-Pro-Display"), url("../fonts/SF-Pro-Display-Regular.woff2") format("woff2"), url("../fonts/SF-Pro-Display-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "SF Pro Display";
  src: local("SF Pro Display Bold"), local("SF-Pro-Display-Bold"), url("../fonts/SF-Pro-Display-Bold.woff2") format("woff2"), url("../fonts/SF-Pro-Display-Bold.woff") format("woff");
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Space Age Cyrillic";
  src: local("spaceagecyrillic-regular"), local("spaceagecyrillic-regular"), url("../fonts/spaceagecyrillic-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
a {
  text-decoration: none;
  color: inherit;
  font-family: "SF Pro Display";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
}

body {
  color: #fff;
  font-family: "SF Pro Display";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}

.container {
  max-width: 1220px;
  padding: 0px 10px;
  margin: 0px auto;
}

.button {
  background-color: transparent;
  font-family: "SF Pro Display";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  text-transform: uppercase;
  padding: 15px 10px;
  cursor: pointer;
  text-align: center;
  border: 1px solid #fff;
  color: white;
}

.header {
  background-image: url(../images/header-bg.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.header__inner {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
}
.header__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 75px 0 30px;
}
.header .menu__list {
  display: flex;
}
.header .menu__item {
  margin-right: 30px;
}
.header .menu__link {
  text-transform: uppercase;
  font-size: 18px;
  line-height: 21px;
  transition: all 0.5s;
}
.header .menu__link:hover {
  color: #cb0e0e;
}
.header .menu-btn {
  display: none;
  position: relative;
  z-index: 20;
  height: 30px;
}
.header .phone {
  font-size: 24px;
  line-height: 29px;
  margin-left: auto;
}
.header .login {
  border: none;
  background-color: transparent;
  cursor: pointer;
  margin-left: 22px;
  margin-right: 134px;
}
.header__content {
  max-width: 600px;
}
.header__title {
  font-family: "Space Age Cyrillic";
  font-size: 60px;
  line-height: 60px;
  font-weight: 400;
  margin-bottom: 20px;
}
.header__title span {
  font-family: "Space Age Cyrillic";
  font-size: 110px;
}
.header__text {
  font-family: "SF Pro Display";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 50px;
}
.header__btn {
  color: white;
  width: 100%;
}
.header__description {
  display: flex;
  justify-content: end;
  margin-top: 40px;
  margin-bottom: 5%;
}
.header__description-text {
  width: 165px;
  font-size: 14px;
  line-height: 17px;
}
.header__description-text + .header__description-text {
  margin-left: 52px;
}

.title {
  font-family: "Space Age Cyrillic";
  padding: 100px 0px;
  font-size: 48px;
  line-height: 48px;
  font-weight: 400;
}

.about {
  background-image: url(../images/main-bg.jpg);
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
}
.about__item {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding-bottom: 150px;
}
.about__item:nth-child(odd) {
  flex-direction: row-reverse;
}
.about__img {
  max-width: 100%;
}
.about__content {
  width: 600px;
}
.about__content .button {
  width: 300px;
  font-size: 18px;
  line-height: 21px;
}
.about__content-title {
  font-size: 30px;
  line-height: 36px;
  font-weight: 700;
  margin-bottom: 5px;
}
.about__content-subtitle {
  font-family: "SF Pro Display";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  color: #036cca;
  margin-bottom: 30px;
}
.about__content-text {
  display: flex;
  justify-content: space-between;
  margin-bottom: 50px;
}
.about__content-text p {
  width: 165px;
}
.about__table {
  margin-bottom: 50px;
}
.about__table-caption {
  caption-side: bottom;
  text-align: left;
  margin-top: 20px;
  font-size: 12px;
  line-height: 14px;
}
.about__table-tr {
  border: 1px solid #fff;
  border-left: none;
  border-right: none;
}
.about__table-tr span {
  display: block;
}
.about__table-tr + .about__table-tr {
  font-weight: 700;
  font-size: 36px;
  line-height: 43px;
  text-align: center;
}
.about__table-tr + .about__table-tr span {
  font-size: 14px;
  line-height: 17px;
}
.about__table-td {
  padding: 10px 30px 10px 0;
}

.how-to {
  background-image: url(../images/how-to-bg.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 100px;
}
.how-to__box {
  text-align: center;
}
.how-to__box circle,
.how-to__box text,
.how-to__box foreignObject {
  transition: all 0.5s;
  cursor: pointer;
  font-size: 18px;
}
.how-to__box g:hover circle {
  r: 20px;
  fill: #036cca;
}
.how-to__box g:hover text {
  fill: #036cca;
  font-size: 26px;
}
.how-to__box g:hover foreignObject {
  color: #036cca;
  font-size: 22px;
}
.how-to__box foreignObject {
  text-align: left;
}
.how-to__box-list {
  display: none;
}

.to-book {
  background-image: url(../images/it_time.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
}
.to-book__inner {
  display: flex;
  justify-content: space-between;
}
.to-book__text {
  width: 165px;
  font-size: 14px;
  line-height: 17px;
  margin: 100px 52px 0 auto;
}
.to-book__text + .to-book__text {
  margin-left: 0;
  margin-right: 0;
}

.info {
  background-image: url(../images/special-offer.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 100px;
}

.special-offer__inner {
  display: flex;
  justify-content: space-between;
}
.special-offer__item {
  width: 500px;
}
.special-offer__item-box {
  padding: 40px 75px;
  text-align: center;
  border: 1px solid #fff;
  margin-bottom: 25px;
  min-height: 400px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.special-offer__item-text {
  font-size: 24px;
  line-height: 29px;
  width: 236px;
  margin: 0 auto;
}
.special-offer__item-text span {
  color: #cb0e0e;
}
.special-offer__old-price {
  text-align: right;
  font-size: 14px;
  line-height: 17px;
  margin-bottom: 16px;
  position: relative;
}
.special-offer__old-price::before {
  content: "";
  position: absolute;
  width: 68px;
  height: 1px;
  background-color: #cb0e0e;
  top: 7px;
  right: 0px;
  transform: rotate(15deg);
}
.special-offer__new-price {
  font-size: 48px;
  line-height: 57px;
}
.special-offer__btn {
  width: 100%;
}

.services {
  padding-bottom: 200px;
}
.services__inner {
  display: flex;
  justify-content: space-between;
}
.services__box-list {
  width: 620px;
  margin-right: 100px;
  font-size: 24px;
  line-height: 29px;
}
.services__list-title {
  margin-bottom: 25px;
}
.services__list {
  list-style: none;
  margin-bottom: 25px;
}
.services__list-item {
  padding-left: 36px;
  position: relative;
}
.services__list-item::before {
  content: "";
  position: absolute;
  width: 20px;
  height: 20px;
  background-color: #fff;
  border-radius: 50%;
  left: 0;
  top: 4px;
}
.services__description {
  font-size: 12px;
  line-height: 14px;
}
.services__buttons {
  max-width: 500px;
}
.services__btn {
  width: 100%;
}
.services__btn--first {
  margin-bottom: 50px;
  position: relative;
}
.services__btn--first::before {
  content: "";
  position: absolute;
  right: 0;
  bottom: 43px;
  background-image: url(../images/space-man.png);
  width: 192px;
  height: 150px;
}

.slider {
  text-align: center;
}
.slider__inner {
  margin-bottom: 50px;
  border: 1px solid white;
  padding: 100px 0;
}
.slider__description {
  font-family: "Space Age Cyrillic";
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
}
.slider__box-img {
  margin: 0 auto;
  width: 70%;
}
.slider__arrow {
  cursor: pointer;
  position: absolute;
  top: 50%;
  z-index: 5;
}
.slider__arrow-left {
  left: 75px;
}
.slider__arrow-right {
  right: 75px;
}

.contacts {
  background-color: #010203;
  padding-bottom: 100px;
}
.contacts__inner {
  display: flex;
  justify-content: space-between;
}
.contacts__box {
  width: 500px;
}
.contacts__title {
  padding-bottom: 46px;
}
.contacts__box-text {
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 80px;
}
.contacts__form-input {
  background-color: #010203;
  border: none;
  border-bottom: 1px solid #fff;
  margin-bottom: 30px;
  padding: 20px 0;
  width: 100%;
}
.contacts__form-input::placeholder {
  color: #fff;
  font-family: "SF Pro Display";
  font-size: 14px;
  line-height: 17px;
}
.contacts__btn {
  margin-top: 70px;
}
.contacts__guarantees {
  width: 500px;
}
.contacts__guarantees-text {
  font-size: 24px;
  line-height: 29px;
}
.contacts__guarantees-img {
  display: block;
  margin-left: auto;
}

.footer {
  background-image: url(../images/footer-bg.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top center;
  padding: 100px 0px 150px;
}
.footer__inner {
  display: flex;
  justify-content: flex-end;
}
.footer__text {
  width: 165px;
  margin-right: 50px;
}
.footer__text p + p {
  margin-top: 50px;
}
.footer__menu {
  list-style: none;
  width: 120px;
  margin-right: 95px;
}
.footer__menu-item, .footer__links-item {
  margin-bottom: 20px;
}
.footer__links {
  list-style: none;
  width: 165px;
}
.footer__links-link, .footer__menu-link {
  text-transform: uppercase;
  transition: all 0.5s;
}
.footer__links-link:hover, .footer__menu-link:hover {
  color: red;
}

@media (max-width: 1160px) {
  .about__item {
    display: block;
    text-align: center;
  }
  .about__img {
    margin-bottom: 50px;
  }
  .about__content {
    width: 100%;
  }
  .about__content-text {
    display: block;
  }
  .about__content-text p {
    width: 100%;
    margin-bottom: 20px;
  }
  .about__table {
    width: 100%;
  }
}
@media (max-width: 1050px) {
  .special-offer__inner {
    display: block;
  }
  .special-offer__item {
    width: 100%;
  }
  .special-offer__btn {
    margin-bottom: 20px;
  }

  .contacts__inner {
    display: block;
  }
  .contacts__box {
    width: 100%;
  }
  .contacts__guarantees {
    margin: 0 auto;
  }
}
@media (max-width: 1024px) {
  .header .login {
    margin-right: 0px;
  }

  .to-book {
    background-position: top center;
  }
  .to-book__inner {
    display: block;
  }
  .to-book__text {
    width: 100%;
    margin-top: 0;
    margin-left: 0;
    margin-right: 0;
    margin-bottom: 30px;
  }
}
@media (max-width: 895px) {
  .header {
    background-position: center right;
  }
  .header .login {
    margin-right: 0px;
  }
  .header__top {
    padding-top: 30px;
  }
  .header .menu-btn {
    display: block;
    width: 40px;
    padding: 0;
    background: none;
    color: inherit;
    border: none;
    font: inherit;
    cursor: pointer;
    outline: inherit;
  }
  .header .menu-btn span {
    display: block;
    height: 2px;
    background-color: #fff;
    position: relative;
  }
  .header .menu-btn span::before, .header .menu-btn span::after {
    content: "";
    width: 40px;
    height: 2px;
    background-color: #fff;
    position: absolute;
    left: 0px;
  }
  .header .menu-btn span::before {
    top: -10px;
  }
  .header .menu-btn span::after {
    top: 10px;
  }
  .header .menu__list {
    transform: translateY(-150%);
    transition: all 0.5s;
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    background-color: #010203;
    padding-top: 90px;
    padding-bottom: 30px;
    width: 100%;
    text-align: center;
  }
  .header .menu__list.active {
    transform: translateY(0);
  }
  .header .menu__item {
    padding: 15px 0;
  }
  .header .menu__link {
    display: block;
  }
  .header .phone,
.header .login {
    position: relative;
    z-index: 20;
  }
  .header__content {
    margin: 0 auto;
  }
  .header__description {
    justify-content: center;
  }

  .how-to__box svg {
    display: none;
  }
  .how-to__box-list {
    display: block;
    padding-left: 40px;
    list-style: none;
  }
  .how-to__box-item {
    padding-bottom: 20px;
    text-align: left;
    padding-left: 20px;
    margin-left: 20px;
    position: relative;
    border-left: 1px solid #fff;
  }
  .how-to__box-item::before {
    content: "";
    position: absolute;
    left: -10px;
    top: 0px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: #fff;
  }
  .how-to__box-item:last-child {
    border-left: none;
  }

  .services__inner {
    display: block;
  }
  .services__box-list {
    width: 100%;
    margin-right: 0;
    margin-bottom: 170px;
  }
  .services__buttons {
    margin: 0 auto;
    width: auto;
    max-width: 500px;
  }

  .slider__inner {
    padding: 0px;
  }
}
@media (max-width: 720px) {
  .services {
    padding-bottom: 50px;
  }

  .slider__inner {
    padding: 0;
  }
  .slider__description {
    font-size: 18px;
    line-height: 24px;
  }

  .info {
    padding-bottom: 50px;
  }
}
@media (max-width: 620px) {
  .header__content {
    text-align: center;
  }
  .header__title {
    font-size: 34px;
    line-height: 30px;
  }
  .header__title span {
    font-size: 60px;
  }
  .header__text {
    justify-content: center;
    font-size: 18px;
  }
  .header__text p {
    padding: 0 10px;
  }
  .header__btn {
    font-size: 18px;
    line-height: 24px;
    width: 60%;
  }
  .header__description {
    display: block;
  }
  .header__description-text {
    width: 100%;
    text-align: center;
  }
  .header__description-text + .header__description-text {
    margin-left: 0;
    margin-top: 20px;
  }

  .title {
    padding: 50px 0;
    font-size: 38px;
    line-height: 38px;
  }

  .about__item {
    padding-bottom: 50px;
  }
  .about__table-tr:first-child {
    display: none;
  }
  .about__table-tr + .about__table-tr {
    font-size: 20px;
    line-height: 22px;
    font-weight: 400;
    border-bottom: none;
  }
  .about__table-td {
    display: block;
    text-align: right;
    border-bottom: 1px solid #fff;
  }
  .about__table-td span {
    display: inline-block;
  }
  .about__table-td::before {
    content: attr(aria-label);
    float: left;
  }
  .about .button {
    width: 250px;
  }

  .to-book {
    background-position: top left;
  }

  .contacts__guarantees {
    width: 100%;
  }
  .contacts__guarantees-img {
    max-width: 100%;
    margin-left: 0;
  }
  .contacts__guarantees-text {
    font-size: 20px;
    line-height: 26px;
  }
}
@media (max-width: 520px) {
  .footer {
    text-align: center;
    padding: 100px 0px;
  }
  .footer__inner {
    display: block;
  }
  .footer__text {
    width: 100%;
    margin-right: 0;
  }
  .footer__text p + p {
    margin-top: 10px;
    margin-bottom: 30px;
  }
  .footer__menu, .footer__links {
    margin-right: 0;
    width: 100%;
  }
  .footer__menu {
    margin-bottom: 50px;
  }
}
@media (max-width: 480px) {
  .title {
    font-size: 28px;
    line-height: 28px;
  }

  .special-offer__price {
    font-size: 34px;
    line-height: 44px;
  }
  .special-offer__item-box {
    padding: 20px 10px;
  }

  .services__box-list {
    font-size: 18px;
    line-height: 26px;
  }
  .services__title {
    font-size: 18px;
  }
  .services__btn--first::before {
    bottom: 62px;
  }

  .slider__box-img {
    width: 30%;
  }

  .to-book__title {
    font-size: 18px;
  }
}
@media (max-width: 360px) {
  .header__top {
    flex-wrap: wrap;
  }
  .header .phone {
    width: 100%;
    margin-top: 20px;
    text-align: center;
  }
  .header .login {
    order: -1;
    margin-left: 0;
  }
  .header .menu {
    order: -1;
  }
  .header .menu__list {
    padding-top: 120px;
  }
  .header .menu__item {
    margin-right: 0;
  }
  .header__title {
    font-size: 28px;
  }
  .header__title span {
    font-size: 32px;
  }
  .header__btn {
    font-size: 16px;
    width: 100%;
  }
}